@import "../utils/variables";

.product-card {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  border: 1px solid $border-color;
  border-radius: 5px;
  background-color: $background-color-white;
  transition: transform 0.3s ease-in;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__title {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin: 0 0 4px;
    color: $text-color-main-black;

    &:hover {
      color: #34568b;
    }
  }

  &__code {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $text-color-secondary-grey;
    margin-bottom: $margin-bottom;
    font-style: normal;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    font-family: $font-family;
    color: $text-color-main-black;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: $margin-bottom;
    align-items: center;
    color: $text-color-main-black;

    &-word {
      font-size: 12px;
      font-weight: 400;
    }

    &-value {
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 700;
    text-decoration: none;
    cursor: pointer;
    color: $background-color-white;
    background-color: $button-color;
    border: 1px solid $button-color;
    width: 166px;
    height: 40px;
    line-height: 16px;
    border-radius: 5px;
    transition: background-color 0.3s color 0.3s ease-in;
  }

  &__link:hover {
    background-color: $background-color-white;
    color: $button-color;
  }
}
