.nav {
  &__item {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    padding: 0;
    margin: 0;
    height: 60px;
  }

  &__link {
    &:link {
      text-decoration: none;
      display: flex;
      align-items: center;
      position: relative;
      height: 100%;
      color: $text-color-main-black;
    }

    &:hover {
      color: $button-color;
      cursor: pointer;
    }
  }

  .is-active {
    color: $button-color;

    &::after {
      position: absolute;
      content: "";
      display: block;
      height: 4px;
      width: 100%;
      border-radius: 8px;
      background-color: $button-color;
      bottom: 0;
    }
  }
}
